/* Import Spotify font */
@import url('https://fonts.googleapis.com/css2?family=Spotify&display=swap');

/* Define Spotify green color */
:root {
  --spotify-green: #1DB954;
}

/* Body styles */
body {
  font-family: 'Spotify', sans-serif;
  background-color: #121212;
  color: #fff;
  margin: 0;
  padding: 0;
}

/* Navbar styles */
.navbar {
  display: flex;
  justify-content: flex-end;
  padding: 20px;
  position: sticky;
  top: 0;
  background-color: #121212;
  z-index: 999;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  margin-right: 20px;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

.navbar a:hover {
  color: var(--spotify-green);
}

/* Container styles */
.container {
  padding: 50px;
  position: relative;
}

/* Heading styles */
h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

/* Paragraph styles */
p {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

/* List styles */
ul {
  list-style-type: none;
  padding: 0;
}

/* Project styles */
.projects-section {
  padding: 100px 0;
}

.projects-grid {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.project {
  background-color: #212121;
  color: #fff;
  border-radius: 10px;
  padding: 20px;
  margin: 20px;
  width: 300px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.project:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

/* About Me Section */
.about-me-section {
  background-color: #1DB954;
  color: #fff;
  padding: 100px 0;
  text-align: center;
}

.about-me-text {
  max-width: 800px;
  margin: 0 auto 40px;
}

/* Contact Section */
.contact-section {
  background-color: #212121;
  color: #fff;
  padding: 100px 0;
}

/* Form styles */
form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

form label {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

form input,
form textarea {
  width: 300px;
  padding: 10px;
  margin-bottom: 20px;
}

form button {
  width: 150px;
  padding: 10px;
  background-color: var(--spotify-green);
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #1DB954;
}

/* Footer styles */
footer {
  background-color: #121212;
  color: #fff;
  padding: 20px;
  text-align: center;
}
